/* –û—Å–Ω–æ–≤–Ω—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è –≤–≤–æ–¥–∞ –∏–≥—Ä–æ–∫–æ–≤ */
.player-input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  padding: 20px;
  border: 2px solid #4CAF50;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 500px;
}

h1 {
  margin-bottom: 20px;
  color: #4CAF50;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 18px;
}

input[type="text"]:focus {
  border-color: #4CAF50;
  outline: none;
}

button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ */
.error-message {
  color: #f44336;
  margin-top: 10px;
}

/* –ù–æ–≤—ã–π —Å—Ç–∏–ª—å –¥–ª—è —Å–ø–∏—Å–∫–∞ –∏–≥—Ä–æ–∫–æ–≤ */
.player-list {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #4CAF50;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.player-list h2 {
  margin: 0;
  font-size: 20px;
  color: #4CAF50;
}

.player-list ul {
  list-style-type: none;
  padding: 0;
}

.player-list li {
  padding: 5px;
  font-size: 18px;
  color: #333;
  border-bottom: 1px solid #ddd;
}

.player-list li:last-child {
  border-bottom: none; /* –£–±–∏—Ä–∞–µ–º –Ω–∏–∂–Ω—é—é –≥—Ä–∞–Ω–∏—Ü—É —É –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ */
}
